@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  padding: 0;
  background-color: #E7E7E7;
}



.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header {
    height: 100px;
    width: 100%;
}
.header .text {
    float: left;
    height: 60px;
    width: 300px;
    margin-left:50px;
    margin-top: 25px;
    font-family: 'Roboto',sans-serif;
    font-weight: bold;
}
.header .list {
   height: 60px;
   width: 430px;
   float: right;
   margin-right:30px;
   margin-top:25px;
}
.header .list ul {
    list-style: none;
}
.header .list ul li {
    display: block;
    float: left;
    height: 40px;
    width: 100px;
    text-align: center;
    font-family: 'Roboto',sans-serif;
    font-size: 18px;
}
.header .list ul li a{
    color:black;
    text-decoration: none;
    border-bottom: 2px solid red;
}
.header .list ul li a:hover {
    cursor: pointer;
}
.header .list ul li:hover {
    cursor: pointer;
}
.box {
    height: 400px;
    width: 60%;
    margin-left: 20%;
    margin-top:50px;
    float: left;
    background-color: white;
    border-radius: 15px;
    box-shadow: 1px 1px 20px #BFBFBF;
}
.box .left {
    height: 100%;
    width: 30%;
    float: left;
}
.box .left img {
    height: 150px;
    width: 150px;
    border-radius:100px;
    float: left;
    margin-top: 35%;
    margin-left:30%;
}
.box .right {
    height: 100%;
    width: 69%;
    float: right;
}
.box .right .info {
    float: left;
    margin-left:30px;
    margin-top: 50px;
}
.box .right .info h1 {
    text-align: left;
    font-size: 46px;
    font-family: 'Roboto',sans-serif;
    color: #FF5858;
    line-height: 30px;
}
.box .right .info p {
    text-align: left;
    line-height:10px;
    font-weight: 400;
    font-size: 20px;
    font-family: 'Roboto',sans-serif;
}
.box .right .info .gender {
    color: #959595;
}
.box .right .info span {
    color: #BF4EFF;
}

.boxes {
    height: auto;
    width: 94%;
    margin-left: 4%;
    float: left;
    margin-top: 15px;
    padding-bottom: 30px;
}

.boxes ul {
    list-style: none;
    margin-left: -60px;
}
.boxes ul li {
    height: 130px;
    width: 300px;
    float: left;
    background-color: white;
    margin-left: 15px;
    border-radius: 10px;
    box-shadow: 1px 1px 10px #BFBFBF;
    margin-top: 30px;
    text-align: left;

}
.boxes ul li .boxed:nth-child(1) {
    margin-left:30px;
    font-family: 'Roboto',sans-serif; 
}
.boxes ul li .boxed:nth-child(2) {
    margin-left:30px;
    font-family: 'Roboto',sans-serif; 
    color:black;
}
.boxes ul li .boxed h2 {
    font-size: 18px;
}
.boxes ul li .boxed h3 {
    font-weight: 300;
    font-size: 16px;
}
.boxes ul li .boxed .h3 {
    color: red;
}
.boxes ul li:nth-child(1) {
    background-color: #BF4EFF;
    color: white;
}
